@import "../../global.scss";
.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    h1{
        margin-top: 40px;
        overflow: hidden;
        font-size: 100px;
        padding:20px;
        @include mobile{
            font-size:50px;
        }

    }
    ul{

        margin: 10px;
        padding:0;
        list-style: none;
        display: flex;
        @include mobile{
            flex-wrap: wrap ;
        }
        
        li{
            font-size: 22px;
            margin-right: 50px;
            padding: 4px;
            border-radius: 15px;

            cursor: pointer;
            &.active{
                background-color: crimson;
            }
            &:hover{
                font-weight: bolder;
            }
        }

    }
    .container{
        width: 70%;
        height: 100%;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: center;
        overflow: hidden;
        @include mobile{
            width: 100% ;
            overflow: auto;
        }

        .item{
            width: 220px;
            height: 150px;
            border-radius: 10px;
            border: 1px solid rgb(3, 3, 3);
            margin: 20px 40px;
            display: flex;
            align-items: center;
            justify-content:center;
            overflow: hidden;
            color:black;
            position: relative;
            transition: all 0.3s ease;
            cursor:pointer;
            h3{
                position: absolute;
                font-size: 30px;
                opacity: 0;

            }
            img{
                width: 80%;
                height: 80%;
                object-fit: cover;
                z-index: 1;
            }
            &:hover{
                background-color: white(255, 255, 255);
                color: black;
                h3{
                    opacity: 1;
                }
                img{
                    opacity: 0.1;
                    z-index: 0;
                }
            }
        }
    }
}