@import "../../global.scss";

.topbar{
    width: 100%;
    height: 70px;
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    transition: all 2s ease;
    font-size: larger;
    position: fixed;
    top:0;
    z-index: 2;
    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left{
            display: flex;
            align-items: center;
            .logo{
                font-size: 40px;
                font-weight:bolder;
                text-decoration:wavy;
                color:inherit;
                margin-right:40px;

            }
            .itemContainer{
                display: flex;
                align-items: center;
                margin-left: 40px;
                .icon{
                    font-size: 18px;
                    margin-right: 5px;
                    font-weight:bolder;
                    @include mobile{
                        display:none;
                    }
                }
                span{
                    font-size: 15px;
                    font-weight:bolder;
                    color: black;
                    @include mobile{
                        display:none;
                    }
                }

                a{
                    @include mobile{
                        display:none;
                    }
                    text-decoration:none;
                    &:hover{
                        cursor: pointer;
                        text-decoration:underline;


                    }

                }
            }
        }
        .right{
            .hamburger{
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                span{
                    width: 100%;
                    height:3px;
                    background-color: black;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }

        }

    }
    &.active{
        background-color: rgb(2, 2, 2);
        color: bisque;
        transition: all 2s ease;
        .hamburger{
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color:white;
                    transform: rotate(-45deg);
                }
            }
        }
        .left{
            .itemContainer{
                span{
                    font-size: 15px;
                    font-weight:bolder;
                    color: bisque;
                }
            }

        }

    }
} 
